import { takeLatest, put, call, select } from 'redux-saga/effects';
import * as types from 'redux/types';
import authServices from 'services/authServices';
import { isEmpty } from 'lodash';
import summonersArenaHeroesServices from 'services/summonersArenaHeroesServices';
import { sha256 } from 'js-sha256';
import heroesMarketService from 'services/heroesMarketService';
import { ZITGA_SECRET_KEY } from 'constants/common';
import { tokenSAEService } from 'services/tokenSAEService';
import { tokenASGService } from 'services/tokenASGService';
import { RESPONSE_MESS } from 'constants/api';
const gzip = require('gzip-js');

function* login({ payload }) {
  const { username, password } = payload;
  try {
    if (username === 'phi' && password === 'phi') {
      yield put({ type: types.REQUEST_LOGIN_SUCCESS });
      authServices.saveUserLocalStorage({ username, isLogged: true });
    } else {
      yield put({ type: types.REQUEST_LOGIN_FAILED });
    }
  } catch (error) {
    yield put({ type: types.REQUEST_LOGIN_FAILED, error });
  }
}

function* logout() {
  try {
    yield authServices.clearUserLocalStorage();
    window.location.reload();
  } catch (error) {}
}

function* checkAuth() {
  const dataUser = authServices.getUserLocalStorage();
  if (!isEmpty(dataUser)) {
    yield put({ type: types.REQUEST_LOGIN_SUCCESS });
  }
}

function* getBalanceOf() {
  yield call(tokenSAEService.transfer);
  // yield call(heroesMarketService.cancelOffer, 19);
}

function* register({ payload }) {
  const auth = yield select((state) => state.authReducer);
  console.log('auth', auth);
  const hashed_password = sha256(payload.password);
  const nft_address_id = auth.account;
  const timestamp = Date.now();
  const secret_key = ZITGA_SECRET_KEY;
  console.log(payload.userName + hashed_password + nft_address_id + timestamp + secret_key);
  const _data = gzip.zip(
    JSON.stringify({
      0: sha256(payload.userName + hashed_password + nft_address_id + timestamp + secret_key),
      1: payload.userName,
      2: hashed_password,
      3: nft_address_id,
      4: timestamp,
    }),
  );
  console.log('ðŸš€ ~ file: App.jsx ~ line 19 ~ _data', _data);
  const _rq = btoa(new Uint8Array(_data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
  console.log(_rq);

  try {
    const _Rs = yield call(authServices.register, _rq);
    if (_Rs) {
      const errorMessage = 'SUCCESS';
      yield put({ type: types.REGISTER_FAILD, errorMessage });
    }
  } catch (ex) {
    const errorCode = ex.response.data.code;
    console.log('errorCode', errorCode);
    const errorMessage = RESPONSE_MESS[errorCode];
    yield put({ type: types.REGISTER_FAILD, errorMessage });
  }
}

export function* authSaga() {
  yield takeLatest(types.REQUEST_LOGIN, login);
  yield takeLatest(types.REQUEST_CHECK_AUTH, checkAuth);
  yield takeLatest(types.REQUEST_LOGOUT, logout);
  yield takeLatest(types.GET_BALANCE_OF, getBalanceOf);
  yield takeLatest(types.REGISTER, register);
}
