import { IS_READ_ONLY } from 'constants/common';
import { contracts } from 'constants/contracts';
import { ethers, utils } from 'ethers';
let _instanceASGToken, _instanceASGTokenReadOnly;
export const tokenASGService = {
  _getInstance: (isReadOnly) => {
    if (isReadOnly && _instanceASGTokenReadOnly) {
      return _instanceASGTokenReadOnly;
    }
    if (_instanceASGToken) {
      return _instanceASGToken;
    }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const _instanceFunContract = new ethers.Contract(
      contracts.tokenASG.address,
      contracts.tokenASG.abi,
      isReadOnly ? provider : signer,
    );
    if (isReadOnly) {
      _instanceASGTokenReadOnly = _instanceFunContract;
    } else {
      _instanceASGToken = _instanceFunContract;
    }
    return _instanceFunContract;
  },
  async transfer(amount) {
    const _instance = tokenASGService._getInstance();
    console.log('amount', amount);
    if (_instance) {
      console.log('_instance', _instance);
      const _price = utils.parseEther(amount.toString()).toString();
      const _tx = await _instance.transfer('0x0aD635E51271539cbf12603a470Bd8d720C1A4B8', _price);
      const _rs = await _tx.wait();
      if (_rs.status) {
        console.log('success', _rs);
        return true;
      }
    }
  },
  async balanceOf(address) {
    if (!address) return;
    const _instance = tokenASGService._getInstance(IS_READ_ONLY);
    if (_instance) {
      const _tx = await _instance.balanceOf(address);
      return utils.formatEther(_tx).toString();
    }
  },
};
