import { COIN98_IS_ENABLED, WALLET_URL } from 'constants/common';
import { ethers } from 'ethers';
import { useCallback } from 'react';
import { setAccount } from 'redux/modules/auth';
import store from 'redux/store';
// import { getBalanceOf } from 'redux/modules/auth';
// import store from 'redux/store';

export const useWallet = () => {
  const loadSigner = useCallback(async (type) => {
    if (window.ethereum) {
      const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
      if (provider && provider.connection) {
        if (type === WALLET_URL.METAMASK && !window.ethereum.isConnected()) {
          return COIN98_IS_ENABLED;
        }
        if (provider.connection.url !== type) {
          return false;
        }
      }
      await provider.send('eth_requestAccounts', []);
      return provider.getSigner();
    } else {
      return false;
    }
    //  else {
    //   window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!');
    //   const error = new Error('Non-Ethereum browser detected. You should consider trying MetaMask!');
    //   error.name = 'Missing provider';
    //   throw error;
    // }
  }, []);
  const connectWallet = useCallback(async (type) => {
    const signer = await loadSigner(type);
    if (typeof signer === 'object') {
      const accounts = await signer.getAddress();
      if (accounts.length > 0) {
        store.dispatch(setAccount(accounts));
        return accounts;
        // getInfoAterConnected
      }
    } else {
      return signer;
    }
  }, []);
  const isMetaMaskConnected = async () => {
    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
      const signer = provider.getSigner();
      const accounts = await signer.getAddress();
      if (accounts.length > 0) {
        store.dispatch(setAccount(accounts));
      }
      return accounts.length > 0;
    } catch (error) {
      console.log('error', error);
    }
  };
  const getAddress = useCallback((account) => {
    if (account) {
      const _stringDel = account.substring(6, account.length - 4);
      return account.replace(_stringDel, '...');
    }
  }, []);
  return { connectWallet, isMetaMaskConnected, loadSigner, getAddress };
};
