import { STEP_REGISTER } from 'constants/common';
import produce from 'immer';
import * as types from 'redux/types';

const initialState = {
  auth: {
    isLogin: false,
    error: null,
    address: '',
  },
  stepRegister: STEP_REGISTER.ONE,
  isShowRegister: false,
  account: null,
  errorMessage: '',
};

export const authReducer = (state = initialState, action) => {
  return produce(state, (draftState) => {
    switch (action.type) {
      case types.REQUEST_LOGIN:
        draftState.auth.isLogin = false;
        break;

      case types.REQUEST_LOGIN_SUCCESS:
        draftState.auth.isLogin = true;
        break;

      case types.REQUEST_LOGIN_FAILED:
        draftState.auth.isLogin = false;
        draftState.auth.error = action.error;
        break;
      case types.SET_ADDRESS:
        draftState.address = action.address;
        break;
      case types.SET_STEP_REGISTER:
        draftState.stepRegister = action.payload;
        break;
      case types.SHOW_HIDE_REGISTER:
        draftState.isShowRegister = action.payload ? action.payload : !draftState.isShowRegister;
        break;
      case types.SET_ACCOUNT:
        draftState.account = action.payload;
        break;
      case types.REGISTER_FAILD:
        draftState.errorMessage = action.errorMessage;
        break;
      default:
        break;
    }
  });
};
