import axios from 'axios';
import { post } from './httpServices';

class AuthServices {
  saveUserLocalStorage(data = {}) {
    localStorage.setItem('user', JSON.stringify(data));
  }

  getUserLocalStorage() {
    const dataUser = localStorage.getItem('user');
    if (!!dataUser) {
      return JSON.parse(dataUser);
    }
    return {};
  }

  clearUserLocalStorage() {
    localStorage.removeItem('user');
  }
  // register(data) {
  //   return axios.post('http://testapi2.summonersarena.io/auth/v1/register', data, {
  //     headers: { 'content-type': 'text/plain', 'response-type': 'application/json' },
  //   });
  // }
  register(data) {
    return axios.post('https://testapi2.summonersarena.io/auth/v1/register', data, {
      headers: { 'content-type': 'text/plain' },
    });
  }
}

export default new AuthServices();
