import * as types from 'redux/types';

export const login = (username, password) => ({
  type: types.REQUEST_LOGIN,
  payload: {
    username,
    password,
  },
});

export const checkAuth = () => ({
  type: types.REQUEST_CHECK_AUTH,
});

export const logout = () => ({
  type: types.REQUEST_LOGOUT,
});

export const getBalanceOf = () => ({
  type: types.GET_BALANCE_OF,
});

export const setStepRegiste = (step) => ({
  type: types.SET_STEP_REGISTER,
  payload: step,
});

export const register = (data) => ({
  type: types.REGISTER,
  payload: data,
});

export const showHideRegister = (status) => ({
  type: types.SHOW_HIDE_REGISTER,
  payload: status,
});

export const setAccount = (account) => ({
  type: types.SET_ACCOUNT,
  payload: account,
});
