import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import './AppDemo.scss';
import './scss/styles.scss';
import SecureRoute from 'routes/SecureRoute';
import PrivateRoute from 'components/PrivateRoute';
import { RouteBase } from 'constants/routeUrl';
import { checkAuth, getBalanceOf } from 'redux/modules/auth';
// import LoginPage from 'views/Login';
import { useWallet } from 'hooks/useWallet';
// import Register from 'views/Register';
export const UserProfileContext = React.createContext();

const App = () => {
  const dispatch = useDispatch();
  const { isMetaMaskConnected } = useWallet();
  useEffect(() => {
    dispatch(checkAuth());
  }, [dispatch]);

  useEffect(() => {
    isMetaMaskConnected();
  }, [isMetaMaskConnected]);

  //! Render
  return (
    <Router>
      <Switch>
        {/* <Route path={RouteBase.Login} exact component={LoginPage} /> */}
        {/* <Route path={RouteBase.Register} exact component={Register} /> */}
        <Route path={RouteBase.Home} component={SecureRoute} />
      </Switch>
    </Router>
  );
};

export default App;
