import { lazy } from 'react';
import withErrorBoundary from 'components/HOCs/withErrorBoundary';
import { RouteBase } from 'constants/routeUrl';

const HomePage = lazy(() => import('views/Home'));
const DashboardPage = lazy(() => import('views/Dashboard'));
const MarketplacePage = lazy(() => import('views/Marketplace'));
const SpecialSummonPage = lazy(() => import('views/SpecialSummon'));
const MyAccountPage = lazy(() => import('views/MyAccount'));
const DetailHero = lazy(() => import('views/DetailHero'));
const Page404 = lazy(() => import('views/Page404'));

//* For secured route
const routes = [
  { path: RouteBase.Home, exact: true, name: 'Home', component: withErrorBoundary(HomePage) },
  { path: RouteBase.Dashboard, exact: true, name: 'Dashboard', component: withErrorBoundary(DashboardPage) },
  { path: RouteBase.Marketplace, exact: true, name: 'Marketplace', component: withErrorBoundary(MarketplacePage) },
  {
    path: RouteBase.SpecialSummon,
    exact: true,
    name: 'SpecialSummon',
    component: withErrorBoundary(SpecialSummonPage),
  },
  { path: RouteBase.MyAccount, name: 'MyAccount', component: withErrorBoundary(MyAccountPage) },

  { path: RouteBase.DetailHero, name: 'DetailHero', component: withErrorBoundary(DetailHero) },
  { name: '404', component: withErrorBoundary(Page404) },
];

export default routes;
