import images from 'assets/images';
import ConnectWallet from 'components/ConnectWallet';
import { RouteBase } from 'constants/routeUrl';
import { useWallet } from 'hooks/useWallet';
import React, { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { showHideRegister } from 'redux/modules/auth';
import connectWallet from '../../assets/images/connect-wallet.png';

const Header = (props) => {
  const _dispatch = useDispatch();
  const { getAddress } = useWallet();
  const _showHideRegister = useCallback((status) => _dispatch(showHideRegister(status)), [_dispatch]);
  const account = useSelector((state) => state.authReducer.account);
  const [menus, setMenus] = useState([
    {
      path: RouteBase.SpecialSummon,
      title: 'Special Summon',
    },
    {
      path: RouteBase.Marketplace,
      title: 'Marketplace',
    },
    {
      path: RouteBase.Dashboard,
      title: 'Dashboard',
    },
  ]);
  const _connectWallet = useCallback(() => {
    _showHideRegister(true);
  }, [_showHideRegister]);

  // useEffect(() => {
  //   if (account && menus.length === 3) {
  //     setMenus([
  //       ...menus,
  //       {
  //         path: RouteBase.MyAccount,
  //         title: 'My Account',
  //       },
  //     ]);
  //   }
  // }, [account, menus]);
  const handleRanderWallet = useCallback(() => {
    if (account && account.length > 0) {
      return (
        <div className="my-account">
          <div className="item my-acc__address">
            <div className="address__coin">1 SAE</div>
            <div className="address__string">{getAddress(account)}</div>
          </div>
          <div className="item my-acc__link">
            <NavLink className="link" to={RouteBase.MyAccount} activeClassName="link-active">
              My Account
            </NavLink>
          </div>
        </div>
      );
    } else {
      return (
        <>
          <div className="header-header-btn-left connect-wallet" onClick={_connectWallet}>
            <span>Connect Wallet</span>
          </div>
        </>
      );
    }
  }, [account]);
  return (
    <>
      <div className="header">
        <div className="header-container">
          <div className="header-left-wrapper">
            <div className="header-header-btn-left">
              <a href="/">
                <img src={images.banner_logo} alt="summoner" />
              </a>
            </div>
            {menus.map((elm, index) => (
              <NavLink className="link" to={elm.path} activeClassName="link-active" key={index}>
                {elm.title}
              </NavLink>
            ))}
          </div>
          <div className="header-right-wrapper">{handleRanderWallet()}</div>
        </div>
      </div>
      <ConnectWallet />
    </>
  );
};

export default Header;
