import { create } from 'apisauce';
import { API, RESPONSE_CODES } from 'constants/api';

const api = create({
  baseURL: API.BASE_URL,
  timeout: 20000,
  headers: {
    'Content-Type': `application/json`,
  },
});

const returnData = (respone) => {
  if (respone.status === RESPONSE_CODES.SUCCESS) {
    return {
      data: respone.data,
      ok: true,
    };
  } else {
    return {
      ok: false,
      error: respone.data.message,
    };
  }
};

const get = async (url, params) => {
  const respone = await api.get(url, params);
  return returnData(respone);
};
const post = async (url, payload) => {
  const respone = await api.post(url, payload);
  return returnData(respone);
};
const put = async (url, payload) => {
  const respone = await api.put(url, payload);
  return returnData(respone);
};
const deleteApi = async (url, payload) => {
  const respone = await api.delete(url, payload);
  return returnData(respone);
};

/**************Metadata server */

const apiMeta = create({
  baseURL: 'http://35.198.248.251:8250',
  timeout: 20000,
  headers: {
    // 'Content-Type': `application/json`,
    'Accept-Encoding': 'gzip,deflate',
  },
});

const getMeta = async (url, params) => {
  const respone = await apiMeta.get(url, params);
  return returnData(respone);
};

export { get, post, put, deleteApi, getMeta };
